<template>
  <div id="feedback">
    <common-header title="意见反馈"></common-header>
    <MyTextArea placeholder="您在使用的过程中还满意吗？如果您觉得有任何问题，可以向我们反馈，我们会根据您的问题尽快优化我们的产品，谢谢您的支持！"/>
    <div class="upload">
      <UploadImg />
    </div>

    <div class="finish__btn" :class="{'active': false}" style="margin-top:0">提交反馈</div>
  </div>
</template>

<script>
  import MyTextArea from '@/components/TextArea/TextArea';
  import UploadImg from '@/components/UploadImg/UploadImg';

  export default {
    components: {
      UploadImg,
      MyTextArea
    }
  }
</script>

<style lang="scss">
  #feedback {
    #textArea {
      padding-bottom: 0;

      textarea {
        height: 4rem;
        padding: 0.4rem;
        border: 1px solid #eee;
        border-radius: 5px;
      }
    }
  }
</style>



